.animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
/*
	Slide Up
*/
@-webkit-keyframes redactorSlideUp {
  to {
    padding-top: 0;
    padding-bottom: 0;
    height: 0;
  }
}
@keyframes redactorSlideUp {
  to {
    padding-top: 0;
    padding-bottom: 0;
    height: 0;
  }
}
.redactor-slideUp {
  overflow: hidden;
  -webkit-animation-name: redactorSlideUp;
  animation-name: redactorSlideUp;
}
/*
	Slide Down
*/
@-webkit-keyframes redactorSlideDown {
  from {
    height: 0;
    padding-top: 0;
    padding-bottom: 0;
  }
}
@keyframes redactorSlideDown {
  from {
    height: 0;
    padding-top: 0;
    padding-bottom: 0;
  }
}
.redactor-slideDown {
  overflow: hidden;
  -webkit-animation-name: redactorSlideDown;
  animation-name: redactorSlideDown;
}
/*
	Fade In
*/
@-webkit-keyframes redactorFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes redactorFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.redactor-fadeIn {
  -webkit-animation-name: redactorFadeIn;
  animation-name: redactorFadeIn;
}
/*
	Fade Out
*/
@-webkit-keyframes redactorFadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes redactorFadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
.redactor-fadeOut {
  -webkit-animation-name: redactorFadeOut;
  animation-name: redactorFadeOut;
}
/*
	Slide In Down
*/
@-webkit-keyframes redactorSlideInDown {
  from {
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
    visibility: visible;
  }
  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}
@keyframes redactorSlideInDown {
  from {
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
    visibility: visible;
  }
  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}
.redactor-slideInDown {
  -webkit-animation-name: redactorSlideInDown;
  animation-name: redactorSlideInDown;
}
/*
	Slide Out Up
*/
@-webkit-keyframes redactorSlideOutUp {
  from {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  to {
    visibility: hidden;
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
}
@keyframes redactorSlideOutUp {
  from {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
